import Vue from "vue";
import VueI18n from "vue-i18n";

Vue.use(VueI18n);
const messages = {
  en: {
    Home: {
      hotdeal: "FLASHSALE",
      special: "SPECIAL OFFER",
      go2joy: "GO2JOY TOP PICK",
      hotelnew: "NEW HOTEL",
      promotion: "HOT PROMOTION",
      tophotel: "TOP RATE HOTEL",
      language: "Choose language"
    }
  },
  vi: {
    Home: {
      hotdeal: "GIM SC",
      special: "U I T BIT",
      go2joy: "GO2JOY  XUT",
      hotelnew: "KHCH SN Mi",
      promotion: "CHNG TRNH KHUYN MI",
      tophotel: "TOP KHCH SN C BNH CHN",
      language: "Chn ngn ng"
    }
  }
};
const i18n = new VueI18n({
  locale: "vi", // set locale
  fallbackLocale: "vi", // set fallback locale
  messages // set locale messages
});
export default i18n;



